










































































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { validationMixin } from 'vuelidate';
import {maxLength, required,minValue} from 'vuelidate/lib/validators';
import FormTag from '@/components/utilities/FormTag.vue';
import { verifLevels, countries } from '@/store/namespaces';
import CountriesMethods from '@/store/countries/methods/countries.methods';
import VerifLevelsMethods from '@/store/verificationLevels/methods/verificationLevels.methods';
import SvgIcon from '@/components/utilities/SvgIcon.vue';
import { REQ_WHOLESALE_PARTNER_EMPTY_STATE } from '../../emptyStates/verifLevels.emptyState';
import { ReqWholeSaleParter } from '../../interfaces/requests.interface';
import { getSpanishLanCountries, showToast } from '@/utils/global-functions';
import { NationalityCountry } from '@/modules/countries/interfaces/countries.interface';
import { MigrationStatus } from '../../interfaces/migrationStatus.interface';

@Component({
    components: {
        FormTag,
        SvgIcon
    },
    mixins: [validationMixin],
    validations:{
        reqWholeSalePartnerLocal:{
            resid_country: {required},
            migration_status: {required},
            new_resid_client_countries: {required},
            clients_number: {required,minValue:minValue(1)}
        }
    }
})
export default class ThirdStep extends Vue {
    @Prop() reqWholeSalePartner : ReqWholeSaleParter;
    loader = true;
    reqWholeSalePartnerLocal : ReqWholeSaleParter = REQ_WHOLESALE_PARTNER_EMPTY_STATE
    spanishTranslateCountries = [];
    latinSpanishCountries = [];
    currentResidCountry = ''
    currentMigrationStatus = '';

    async mounted() {
        this.reqWholeSalePartnerLocal = this.reqWholeSalePartner;
        await this.fetchMigrationStatus();
        await this.fetchLatinCountries();
        this.spanishTranslateCountries = getSpanishLanCountries(this.countries)
        this.latinSpanishCountries = getSpanishLanCountries(this.latinCountries)
        this.setExistingResponse();
        this.loader = false;
    }

    setExistingResponse(){
        if (typeof this.reqWholeSalePartnerLocal.resid_country === 'string' && this.reqWholeSalePartnerLocal.resid_country.length)
            this.currentResidCountry = this.countries.find(el => el.id_all_country === this.reqWholeSalePartnerLocal.resid_country).spanish_name;
        if (typeof this.reqWholeSalePartnerLocal.migration_status === 'string' && this.reqWholeSalePartnerLocal.migration_status.length){
            this.currentMigrationStatus = this.reqWholeSalePartnerLocal.migration_status;
            this.reqWholeSalePartnerLocal.migration_status = this.migrationStatus.find(el => el.name_migration_status === this.currentMigrationStatus).id_migration_status
        }
        if (typeof this.reqWholeSalePartnerLocal.resid_country === 'number')
            this.currentResidCountry = this.countries.find(el => el.id_all_country === this.reqWholeSalePartnerLocal.resid_country.toString()).spanish_name;
        if (typeof this.reqWholeSalePartnerLocal.migration_status === 'number'){
            this.currentMigrationStatus = this.migrationStatus.find(el => el.id_migration_status === this.reqWholeSalePartnerLocal.migration_status).name_migration_status
        }
        if (typeof this.reqWholeSalePartnerLocal.new_resid_client_countries === 'string' && this.reqWholeSalePartnerLocal.new_resid_client_countries.length){
           this.reqWholeSalePartnerLocal.new_resid_client_countries = this.reqWholeSalePartnerLocal.new_resid_client_countries.split(',')
           this.reqWholeSalePartnerLocal.new_resid_client_countries.forEach(el=>{
               this.reqWholeSalePartnerLocal.new_resid_client_countries_array.push(
                   this.spanishTranslateCountries.find(e=> e.id_all_country === el)
               )
           })
        }
    }

    getNewResidClientCountries(val: any){
        let new_resid_client_countries = [];
        this.reqWholeSalePartnerLocal.new_resid_client_countries_array = val;
        val.forEach(el =>{
            new_resid_client_countries.push(el.id_all_country)
        })
        this.reqWholeSalePartnerLocal.new_resid_client_countries = new_resid_client_countries;
    }

    goBack(){
        this.$emit('prevStep')
    }

    submitForm(){
        this.$v.$touch();
        if (!this.$v.$invalid){
            this.$emit('sendRequestStatus', this.reqWholeSalePartnerLocal)
            this.$emit('nextStep');
        }
        else showToast('Debe contestar todas las preguntas','error')
    }

    @countries.Getter(CountriesMethods.getters.GET_ALL_NATIONALITY_COUNTRIES)
    countries!: NationalityCountry[];

    @verifLevels.Action(VerifLevelsMethods.actions.FETCH_AVAILABLE_RESID_COUNTRIES)
    fetchLatinCountries!:() => Promise<boolean>
    @verifLevels.Action(VerifLevelsMethods.actions.FETCH_MIGRATION_STATUS)
    fetchMigrationStatus!:() => Promise<boolean>
    @verifLevels.Getter(VerifLevelsMethods.getters.GET_AVAILABLE_RESID_COUNTRIES)
    latinCountries!: NationalityCountry[];
    @verifLevels.Getter(VerifLevelsMethods.getters.GET_MIGRATION_STATUS)
    migrationStatus!: MigrationStatus[];
}
