



























































































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { validationMixin } from 'vuelidate';
import {maxLength, required, minLength} from 'vuelidate/lib/validators';
import FormTag from '@/components/utilities/FormTag.vue';
import { verifLevels } from '@/store/namespaces';
import SvgIcon from '@/components/utilities/SvgIcon.vue';
import { REQ_WHOLESALE_PARTNER_EMPTY_STATE } from '../../emptyStates/verifLevels.emptyState';
import { ReqWholeSaleParter } from '../../interfaces/requests.interface';
import VerifLevelMethods from '@/store/verificationLevels/methods/verificationLevels.methods'
import { showToast } from '@/utils/global-functions';
import Loader from '@/components/utilities/Loader.vue';
import { WholeSalePartnerReq } from '../../interfaces/requirement.interface';

@Component({
    components: {
        FormTag,
        SvgIcon,
        Loader
    },
    mixins: [validationMixin],
    validations:{
        reqWholeSalePartner:{
            bussiness_name: {required, maxLength: maxLength(256)},
            web_page_exp: {required},
            logo: {required}
        }
    }
})
export default class FifthtStep extends Vue {
    loader = true;
    @Prop() reqWholeSalePartner : ReqWholeSaleParter;
    reqWholeSalePartnerLocal : ReqWholeSaleParter = REQ_WHOLESALE_PARTNER_EMPTY_STATE
    $refs : any ={}
   
    async mounted() {
        this.reqWholeSalePartnerLocal = this.reqWholeSalePartner;
        this.setExistingResponse()
        this.loader = false;
    }

    setExistingResponse(){
        if (typeof this.reqWholeSalePartnerLocal.logo === 'string' && this.reqWholeSalePartnerLocal.logo.length) this.reqWholeSalePartnerLocal.logo = JSON.parse(this.reqWholeSalePartnerLocal.logo)
        if (typeof this.reqWholeSalePartnerLocal.web_page_exp === 'string' && this.reqWholeSalePartnerLocal.web_page_exp.length) this.reqWholeSalePartnerLocal.web_page_exp = JSON.parse(this.reqWholeSalePartnerLocal.web_page_exp)
    }

    goBack(){
        this.$emit('prevStep')
    }

    setNewStatus(){
        if (this.wholeSalePartnerReqs.questions !== null){
            this.wholeSalePartnerReqs.questions.forEach(el =>{
                if (el.question_apb_ok === false)
                this.reqWholeSalePartnerLocal[el.question_type+'_status'] = null
            })
        }
    }
   
    async submitForm(){
        this.$v.$touch();
        if (!this.$v.$invalid){
            this.$refs.loader.showLoader();
            this.setNewStatus();
            let response = await this.requestWholesale(this.reqWholeSalePartnerLocal)
            if (response.error) showToast(response.message, 'error')
            else {
                showToast(response.message, 'success')
                this.$router.push({name: 'Dashboard'})
            }
            this.$refs.loader.hideLoader();
        }
        else 
            showToast('Debe contestar todas las preguntas', 'error')

    }

    @verifLevels.Getter(VerifLevelMethods.getters.GET_WHOLESALEPARTNER_REQUIREMENTS)
    wholeSalePartnerReqs!: {questions: WholeSalePartnerReq[], email_user: string}
    @verifLevels.Action(VerifLevelMethods.actions.REQUEST_WHOLESALE_PARTNER)
        requestWholesale!: (requirement: ReqWholeSaleParter) => Promise<{message: string, error: boolean}>

}
