































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import Header from '@/components/layout/Header.vue';
import InfoSide from '@/components/InfoSide.vue';
import Stepper from '@/components/utilities/Stepper.vue';
import FirstStep from '@/modules/verificationLevels/components/wholeSale/FirstStep.vue'
import SecondStep from '@/modules/verificationLevels/components/wholeSale/SecondStep.vue'
import ThirdStep from '@/modules/verificationLevels/components/wholeSale/ThirdStep.vue'
import FourthStep from '@/modules/verificationLevels/components/wholeSale/FourthStep.vue'
import FifthStep from '@/modules/verificationLevels/components/wholeSale/FifthStep.vue';
import { auth,verifLevels,countries } from '@/store/namespaces';
import AuthMethods from '@/store/auth/methods/auth.methods';
import VerifLevelMethods from '@/store/verificationLevels/methods/verificationLevels.methods';
import CountriesMethods from '@/store/countries/methods/countries.methods';
import { User } from '@/modules/auth/interfaces/user.interface';
import Loader from '@/components/utilities/Loader.vue';
import { REQ_WHOLESALE_PARTNER_EMPTY_STATE } from '../emptyStates/verifLevels.emptyState';
import { ReqWholeSaleParter} from '../interfaces/requests.interface';
import { WholeSalePartnerReq } from '../interfaces/requirement.interface';
import { showToast } from '@/utils/global-functions';
@Component({
    components: {
        Header,
        InfoSide,
        Stepper,
        FirstStep,
        SecondStep,
        ThirdStep,
        FourthStep,
        FifthStep,
        Loader
    },
})
export default class LevelOne extends Vue {

  registerButton = {
        name: 'Iniciar sesión',
        text: '¿Ya tienes una cuenta?',
        level: 2,
        redirect: '/inicio-sesion',
    };
    $refs : any ={}
    steps= [
        { component: 'FirstStep', label: '' },
        { component: 'SecondStep', label: '' },
        { component: 'ThirdStep', label: '' },
        { component: 'FourthStep', label: '' },
        { component: 'FifthStep', label: '' },

    ]
    currentComponent = this.steps[0].component;
    currentStep = 1;
    reqWholeSalePartner : ReqWholeSaleParter = REQ_WHOLESALE_PARTNER_EMPTY_STATE;
    alreadyRequested = false;
    someFieldsRejected = false;

    
    async mounted(){
        this.$refs.loader.showLoader();
        this.reqWholeSalePartner.email_user = this.userData.email_user;
        if (!await this.fetchAllCountries()){
            showToast('Ha ocurrido un problema al cargar la página', 'error');
            this.$router.push({name: 'Dashboard'})
        }
        if (await this.fetchWholeSalePartnerReqs(this.userData.email_user)){
            this.setRequitements();
        }
       this.$refs.loader.hideLoader();
    }

    setRequitements(){
        if (this.wholeSalePartnerReqs.questions !== null){
            this.alreadyRequested = true
            this.wholeSalePartnerReqs.questions.forEach(el =>{
                this.reqWholeSalePartner[el.question_type] = el.question_value
                this.reqWholeSalePartner[el.question_type+'_status'] = el.question_apb_ok
                if (el.question_apb_ok === false) this.someFieldsRejected = true
            })
        }
    }

    nextStep(){
        this.$refs.step.moveStep(this.currentStep+1);
        this.currentStep += 1;
        this.currentComponent= this.steps[this.currentStep-1].component;
    }

    prevStep(){
        this.$refs.step.moveStep(this.currentStep-1);
        this.currentStep -= 1;
        this.currentComponent= this.steps[this.currentStep-1].component;
    }

    getRequestStatus(request: ReqWholeSaleParter){
        this.reqWholeSalePartner = request;
    }

    @auth.Getter(AuthMethods.getters.GET_USER_DATA)
    userData!: User;

    @verifLevels.Action(VerifLevelMethods.actions.FETCH_WHOLESALEPARTNER_REQUIREMENTS)
    fetchWholeSalePartnerReqs!: (emailUser: string) => Promise<boolean>
    @verifLevels.Getter(VerifLevelMethods.getters.GET_WHOLESALEPARTNER_REQUIREMENTS)
    wholeSalePartnerReqs!: {questions: WholeSalePartnerReq[], email_user: string}

    @countries.Action(CountriesMethods.actions.FETCH_ALL_NATIONALITY_COUNTRIES)
    fetchAllCountries!: () => Promise<boolean>;
}
