





























































import { Component, Prop, Vue, Watch } from 'vue-property-decorator';


@Component({
    components:{
    }
})
export default class FormTag extends Vue {
    @Prop() options! : Array<any>;
    @Prop() tagsMaxLength? : number;
    @Prop() name : string;
    @Prop() keyName : string;
    @Prop() selectedOptions?: Array<any>;
    @Prop() edit?: boolean;
    value: Array<any> = [];
    addTag: any;
    inputAttrs: any;
    search ='';
    currentTags : Array<any> = [];

    clicked = false;


   mounted(){
       this.currentTags = this.selectedOptions
    }

     get criteria() {
        // Compute the search criteria
        return this.search.trim().toLowerCase()
      }

      get availableOptions() {
        const criteria = this.criteria
        // Filter out already selected options
        const options = this.options.filter(el => this.value.indexOf(el) === -1)
        if (criteria) {
          // Show only options that match criteria
          return options.filter(el => el[this.name].toLowerCase().indexOf(criteria) > -1);
        }
        // Show all options available 
        let newOptions : Array<any> = []  
        options.forEach(el =>{
           if (!this.currentTags.find(e => e[this.keyName] === el[this.keyName]))
            newOptions.push(el)
        })
        return newOptions
      }

      onOptionClick( option: any, addTag: any , tags: any) {
       // addTag(option[this.name])
        this.search = ''
        this.currentTags.push(option)
        this.$emit('setTags',this.currentTags)
      }

      remove(removeTag : any, tag: any){
        if (this.edit){
          this.currentTags = this.currentTags.filter(el =>el[this.keyName] !== tag[this.keyName])
          this.$emit('setTags',this.currentTags)
        }
      }

      // getSortoObject(){
      //    let object : any = {}
      //    this.currentTags.forEach(el =>{
      //       object[el[this.keyName]] = el.sorto.toLowerCase();
      //    })
      //    return object
      // }
    
}
