

























































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { validationMixin } from 'vuelidate';
import {maxLength, required, requiredIf} from 'vuelidate/lib/validators';
import FormTag from '@/components/utilities/FormTag.vue';
import { countries } from '@/store/namespaces';
import CountriesMethods from '@/store/countries/methods/countries.methods';
import SvgIcon from '@/components/utilities/SvgIcon.vue';
import { REQ_WHOLESALE_PARTNER_EMPTY_STATE } from '../../emptyStates/verifLevels.emptyState';
import { ReqWholeSaleParter } from '../../interfaces/requests.interface';
import { getSpanishLanCountries, showToast } from '@/utils/global-functions';
import { NationalityCountry } from '@/modules/countries/interfaces/countries.interface';

@Component({
    components: {
        FormTag,
        SvgIcon
    },
    mixins: [validationMixin],
    validations:{
        reqWholeSalePartnerLocal:{
            remittance_service: {required},
            old_resid_client_countries:
                {required: requiredIf(function(){
        	        return this.reqWholeSalePartnerLocal.remittance_service
                })
            },
            profession: {required,  maxLength: maxLength(256)}
        }
    }
})
export default class SecondStep extends Vue {
    @Prop() reqWholeSalePartner : ReqWholeSaleParter;
    loader = true;
    spanishTranslateCountries = [];
    reqWholeSalePartnerLocal : ReqWholeSaleParter = REQ_WHOLESALE_PARTNER_EMPTY_STATE
   
    async mounted() {
        this.reqWholeSalePartnerLocal = this.reqWholeSalePartner;
        this.spanishTranslateCountries = getSpanishLanCountries(this.countries)
        this.setExistingResponse()
        this.loader = false;
    }

    setExistingResponse(){
        if (typeof this.reqWholeSalePartnerLocal.remittance_service === 'string' && this.reqWholeSalePartnerLocal.remittance_service.length) this.reqWholeSalePartnerLocal.remittance_service = JSON.parse(this.reqWholeSalePartnerLocal.remittance_service)
        if (typeof this.reqWholeSalePartnerLocal.old_resid_client_countries === 'string'){
            if (this.reqWholeSalePartnerLocal.old_resid_client_countries.length){
                this.reqWholeSalePartnerLocal.old_resid_client_countries = this.reqWholeSalePartnerLocal.old_resid_client_countries.split(',')
                this.reqWholeSalePartnerLocal.old_resid_client_countries.forEach(el=>{
                    this.reqWholeSalePartnerLocal.old_resid_client_countries_array.push(
                        this.spanishTranslateCountries.find(e=> e.id_all_country === el)
                    )
                })
            }
            else {
                this.reqWholeSalePartnerLocal.old_resid_client_countries = []
            }
        }
    }

    goBack(){
        this.$emit('prevStep')
    }

    getOldResidClientCountries(val: any){
        let old_resid_client_countries = [];
        this.reqWholeSalePartnerLocal.old_resid_client_countries_array = val;
        val.forEach(el =>{
            old_resid_client_countries.push(el.id_all_country)
        })
        this.reqWholeSalePartnerLocal.old_resid_client_countries = old_resid_client_countries;
    }

    submitForm(){
        this.$v.$touch();
        if (!this.$v.$invalid){
        this.$emit('sendRequestStatus', this.reqWholeSalePartnerLocal)
          this.$emit('nextStep');
        }
        else showToast('Debe contestar todas las preguntas','error')       
    }

    @countries.Getter(CountriesMethods.getters.GET_ALL_NATIONALITY_COUNTRIES)
    countries!: NationalityCountry[];
}
