





































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { validationMixin } from 'vuelidate';
import Toast from '@/components/utilities/Toast.vue';
import InputFile from '@/components/utilities/InputFile.vue';
import { required, maxLength } from 'vuelidate/lib/validators';
import { ReqWholeSaleParter } from '../../interfaces/requests.interface';
import {REQ_WHOLESALE_PARTNER_EMPTY_STATE}  from '@/modules/verificationLevels/emptyStates/verifLevels.emptyState'
import { showToast } from '@/utils/global-functions';


@Component({
    components: {
        Toast,
        InputFile
    },
    mixins: [validationMixin],
    validations:{
        reqWholeSalePartner:{
            reasons: {required,  maxLength: maxLength(256),},
            strenghts: {required,  maxLength: maxLength(256)},
        }
    }
})
export default class FirstStep extends Vue {
    @Prop() reqWholeSalePartner : ReqWholeSaleParter;
    loader = true;
    reqWholeSalePartnerLocal : ReqWholeSaleParter = REQ_WHOLESALE_PARTNER_EMPTY_STATE;

    async mounted(){
        this.reqWholeSalePartnerLocal = this.reqWholeSalePartner;
        this.loader = false;
    }

    submitForm(){
        this.$v.$touch();
        if (!this.$v.$invalid){
          this.$emit('sendRequestStatus', this.reqWholeSalePartnerLocal)
          this.$emit('nextStep');
        }
        else showToast('Debe contestar todas las preguntas','error')
    }


}
