



































































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { validationMixin } from 'vuelidate';
import {maxLength, required, minValue} from 'vuelidate/lib/validators';
import FormTag from '@/components/utilities/FormTag.vue';
import { auth, countries } from '@/store/namespaces';
import AuthMethods from '@/store/auth/methods/auth.methods';
import CountriesMethods from '@/store/countries/methods/countries.methods';
import { User } from '@/modules/auth/interfaces/user.interface';
import SvgIcon from '@/components/utilities/SvgIcon.vue';
import { REQ_WHOLESALE_PARTNER_EMPTY_STATE } from '../../emptyStates/verifLevels.emptyState';
import { ReqWholeSaleParter } from '../../interfaces/requests.interface';
import { getSpanishLanCountries, showToast } from '@/utils/global-functions';
import { NationalityCountry } from '@/modules/countries/interfaces/countries.interface';

@Component({
    components: {
        FormTag,
        SvgIcon
    },
    mixins: [validationMixin],
    validations:{
        reqWholeSalePartner:{
            monthly_amount: {required},
            monetary_growth: {required},
            clients_growth: {required, minValue: minValue(1)}
        }
    }
})
export default class FourthStep extends Vue {
    @Prop() reqWholeSalePartner : ReqWholeSaleParter;
    loader = true;
    reqWholeSalePartnerLocal : ReqWholeSaleParter = REQ_WHOLESALE_PARTNER_EMPTY_STATE
   
    async mounted() {
        this.reqWholeSalePartnerLocal = this.reqWholeSalePartner;
        this.loader = false;
    }

    goBack(){
        this.$emit('prevStep')
    }
   
    submitForm(){
        this.$v.$touch();
        if (!this.$v.$invalid){
            this.$emit('sendRequestStatus', this.reqWholeSalePartnerLocal)
            this.$emit('nextStep');
        }
        else showToast('Debe contestar todas las preguntas','error')
    }

  
}
